.main-nav {
  position: relative;
  width: auto;
  display: flex;
  justify-content: space-between;
}
.main-nav__wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 73px;
  box-sizing: border-box;
  padding: 25px;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    margin: 40px;
    padding: 0;
    height: auto;
  }
}
@media (min-width: 1440px) {
  .main-nav__wrapper {
    margin: 21px 60px 12px;
  }
}
.main-nav__toggle {
  display: none;
  border: 0 none;
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    margin-left: 29px;
    margin-top: 22px;
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav__toggle--open {
  display: block;
  width: 25px;
  height: 11px;
  position: relative;
  z-index: 100;
  cursor: pointer;
}
.main-nav__toggle--open::after {
  content: "";
  border-radius: 1px;
  display: block;
  top: 5px;
  left: -3px;
  width: 25px;
  height: 3px;
  position: absolute;
  background-color: #ffffff;
}
.main-nav__toggle--open::before {
  content: "";
  border-radius: 1px;
  display: block;
  top: -3px;
  left: -3px;
  width: 25px;
  height: 3px;
  background-color: #ffffff;
  position: absolute;
}
.main-nav__toggle--open:hover::after,
.main-nav__toggle--open:hover::before {
  background-color: rgba(255, 255, 255, 0.6);
}
.main-nav__toggle--open:active::after,
.main-nav__toggle--open:active::before {
  background-color: rgba(255, 255, 255, 0.6);
}
@media (min-width: 1440px) {
  .main-nav__toggle--open {
    display: none;
  }
}
.main-nav__toggle--close {
  display: block;
  position: relative;
  z-index: 100000;
  cursor: pointer;
}
.main-nav__toggle--close::after,
.main-nav__toggle--close::before {
  content: "";
  width: 22px;
  height: 2px;
  border: 1px;
  background-color: #CCCCCC;
  position: absolute;
  top: 18px;
  right: 0;
}
.main-nav__toggle--close::before {
  transform: rotate(45deg);
}
.main-nav__toggle--close::after {
  transform: rotate(-45deg);
}
.main-nav__toggle--close:hover::after,
.main-nav__toggle--close:hover::before {
  background-color: rgba(204, 204, 204, 0.6);
}
.main-nav__toggle--close:active::after,
.main-nav__toggle--close:active::before {
  background-color: rgba(204, 204, 204, 0.3);
}
@media (min-width: 1440px) {
  .main-nav__toggle--close {
    display: none;
  }
}
.main-nav__list {
  display: none;
}
@media (min-width: 1440px) {
  .main-nav__list {
    list-style: none;
    display: flex;
    margin-left: -193px;
    font-weight: 500;
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
    position: relative;
  }
  .main-nav__list::before {
    content: "";
    height: 2px;
    width: 60px;
    background-color: #ffffff;
    position: absolute;
    top: 7px;
    left: -64px;
  }
}
.main-nav__item a {
  text-decoration: none;
  color: #ffffff;
}
.main-nav__item:nth-child(1) {
  margin-right: 34px;
}
.main-nav__item:nth-child(2) {
  margin-right: 33px;
}
.main-nav__link:hover {
  transition: transform 0.3s;
  position: relative;
  color: #FFD74B;
}
.main-nav__link:hover:before {
  position: absolute;
  top: 100%;
  content: attr(data-hover);
  font-weight: 700;
  transform: translate3d(0, 0, 0);
}
.main-nav__link:active {
  transition: transform 0.3s;
  position: relative;
  color: rgba(255, 215, 75, 0.3);
}
.main-nav--scroll {
  background-color: #ffffff;
}
.main-nav--scroll .main-nav__list::before {
  content: "";
  height: 2px;
  width: 44px;
  background-color: #192144;
  position: absolute;
  top: 7px;
  left: -64px;
}
.main-nav--scroll .main-nav__link {
  color: #192144;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
}
.main-nav--scroll .logo__img--scroll {
  display: block;
}
@media (min-width: 768px) {
  .main-nav--scroll .logo__img--scroll {
    margin-top: 10px;
  }
}
.main-nav__toggle--scroll {
  border-bottom: 3px solid #CCCCCC;
}
.main-nav__toggle--scroll::after {
  content: "";
  border-radius: 1px;
  display: block;
  top: 0;
  left: 0;
  width: auto;
  height: 3px;
  background-color: #CCCCCC;
}
.main-nav__toggle--open .main-nav__logo--white {
  display: block;
}
.main-nav__toggle--open .main-nav__logo--blue {
  display: none;
}
.main-nav__toggle--close .main-nav__logo--blue {
  display: block;
}
.main-nav__toggle--close .main-nav__logo--white {
  display: none;
}
